@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&family=Ubuntu:wght@400;500;700&display=swap");
:root {
  --light-red: hsl(356, 100%, 66%);
  --very-light-red: hsl(355, 100%, 74%);
  --very-dark-blue: hsl(288, 49%, 24%);
  --white: hsl(0, 0%, 100%);
  --grayish-blue: hsl(240, 2%, 79%);
  --very-dark-grayish-blue: hsl(207, 13%, 34%);
  --very-dark-black-blue: hsl(240, 10%, 16%);
  --background-gradient-intro-cta: linear-gradient(
    to right,
    hsl(13, 100%, 72%),
    hsl(353, 100%, 62%)
  );
  --background-gradient-body: linear-gradient(
    to left,
    hsl(237, 17%, 21%),
    hsl(237, 23%, 32%)
  );
}

:root {
  --font-overpass: "Overpass", sans-serif;
  --font-ubuntu: "Ubuntu", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-ubuntu);
}

h1,
h2,
h3,
h4,
p {
  margin: inherit;
  padding: inherit;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

ul {
  list-style: none;
  margin: inherit;
  padding: inherit;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.875rem;
  font-weight: 600;
}

h1 {
  font-size: 2.8125rem;
}

.container {
  padding-inline: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4.375rem;
  position: absolute;
  top: 2.5rem;
  left: 50%;
  color: var(--white);
  width: 80%;
  transform: translateX(-50%);
  z-index: 2;
}
.header .logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .nav {
  display: flex;
  gap: 2rem;
  margin-right: auto;
}
.header .nav > ul > h4:hover {
  text-decoration: underline;
}
.header .nav > ul > li {
  display: none;
}
.header .nav > ul > li:hover {
  font-weight: 700;
}
.header #signup {
  color: var(--light-red);
  background: var(--white);
  padding: 0.9375rem 2.5rem;
  border-radius: 1.875rem;
}
.header #signup:hover {
  background: rgba(255, 255, 255, 0.3);
  color: var(--white);
}
.header .menu {
  display: none;
}
@media (max-width: 71.8125em) {
  .header .nav,
.header .buttons {
    display: none;
  }
  .header .menu {
    display: flex;
    justify-content: center;
  }
}

.hero {
  position: relative;
  color: var(--white);
  width: 100%;
  height: 600px;
  border-bottom-left-radius: 6.25rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.hero::before {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background: var(--background-gradient-intro-cta);
  z-index: -2;
}
.hero::after {
  content: "";
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  background: no-repeat center left/150% url("/images/bg-pattern-intro.svg");
  z-index: -1;
}
.hero .subtitle {
  margin-top: 1.5625rem;
  margin-bottom: 2.8125rem;
  font-size: 1.125rem;
}
.hero #free {
  color: var(--light-red);
  background: var(--white);
  padding: 0.9375rem 0;
  border-radius: 1.875rem;
  width: 8.125rem;
}
.hero #free:hover {
  color: var(--white);
  background: rgba(255, 255, 255, 0.3);
}
.hero #more {
  padding: 0.9375rem 0;
  width: 8.125rem;
  border: 1px solid var(--white);
  border-radius: 1.875rem;
}
.hero #more:hover {
  color: var(--light-red);
  background: var(--white);
}

.design {
  background: no-repeat top 3.125rem right -17.5rem/auto url("/images/illustration-editor-desktop.svg");
  padding: 2.5rem;
}
.design > h2 {
  text-align: center;
}
.design .text-container {
  width: 50%;
  padding: 2.5rem;
}